import React from 'react';
import './App.css';
import {HashRouter} from "react-router-dom";
import AppRouting from "./platform/AppRouting";
import HomeAppProvider from "./winery/HomeApp/HomeAppContext";

function App()
{
    return (
        <HashRouter basename={process.env.PUBLIC_URL + '/'}>
            <HomeAppProvider>
                <AppRouting/>
            </HomeAppProvider>
        </HashRouter>
    );
}

export default App;
