import React, {useCallback, useContext, useEffect, useMemo, useReducer} from 'react';

interface HomeAppContextInterface
{
    isMobile: boolean;
    isMenuOpen: boolean;
    pageWidth: number,
    pageHeight: number,
    closeMenu: () => void;
    toggleOpenMenu: () => void;
    setIsMobile: (value: boolean) => void;
    setState: (name: string, value: any) => void;
}

const defaultState = {
    pageWidth: window.innerWidth,
    pageHeight: window.innerHeight,
    isMobile: window.innerWidth < 960,
    isMenuOpen: false,
    closeMenu: () => {},
    toggleOpenMenu: () => {},
    setIsMobile: () => {},
    setState: () => {}
};

const reducer = (state: any, action: any) => {
  switch (action.type)
  {
      case "toggleOpenMenu":
          return {...state, isMenuOpen: !state.isMenuOpen};
      case "setIsMobile":
          return {...state, isMobile: action.value};
      case "closeMenu":
          return {...state, isMenuOpen: false};
      default:
          return {...state,  [action.type]: action.value};
  }
};

const HomeAppContext = React.createContext<HomeAppContextInterface>(defaultState);
export const useHomeAppContext = (): HomeAppContextInterface => {
    return useContext(HomeAppContext);
};

const HomeAppProvider: React.FC = ({children}) => {

    const [state, dispatch] = useReducer(reducer, defaultState);

    useEffect(() => {
        if (state.isMobile)
        {
            document.body.classList.add("mobile_layout");
        }
        else
        {
            document.body.classList.remove("mobile_layout");
        }
    },[state.isMobile])

    const toggleOpenMenu = useCallback(() => {
        dispatch({type: "toggleOpenMenu"});
    }, []);

    const closeMenu = useCallback(() => {
        dispatch({type: "closeMenu"});
    }, []);

    const setIsMobile = useCallback((isMobile: boolean) => {
        dispatch({type: "setIsMobile", value: isMobile});
    }, []);

    const setState = useCallback((name: string, value: boolean | string | number) => {
        dispatch({type: name, value});
    }, []);

    useEffect(() => {
        const updateDimensions = () => {
            console.log(window.innerWidth)
            setState("pageWidth", window.innerWidth)
            setState("pageHeight", window.innerHeight)
            setIsMobile(window.innerWidth < 960);
        };

        window.addEventListener("resize", () => updateDimensions());
        return window.removeEventListener("resize", () => updateDimensions());
    }, []);

    const providerValue = useMemo(() => ({
        isMobile: state.isMobile,
        isMenuOpen: state.isMenuOpen,
        pageWidth: state.pageWidth,
        pageHeight: state.pageHeight,
        closeMenu,
        toggleOpenMenu,
        setIsMobile,
        setState
    }), [state.isMobile, state.isMenuOpen, state.pageWidth, state.pageHeight, closeMenu, toggleOpenMenu, setIsMobile, setState]);

    return (
        <HomeAppContext.Provider value={providerValue}>
            {children}
        </HomeAppContext.Provider>
    )
};

export default HomeAppProvider;